/*! HOME-IMG !*/

@font-face {
    font-family: "Montserrat";
    src: url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap') format('truetype');
    font-weight: normal;
    font-style: normal;
}

main #home-page-img {
    border-radius: 24px;
    margin-bottom: 100px;
    position: relative;
}

main .image-wrapper {
    position: relative;
}

main .text-conteiner {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}

main .overlay-text {
    background-color: #00000033;
    border-radius: 24px;
    position: absolute;
    bottom: 130px;
    display: flex;
    flex-direction: column;
    align-items: center;
    backdrop-filter: blur(10px);
}

.text,
.text1,
.text2 {
    margin: 16px 0px;
    color: #fffdfd;
    margin: 0;
    text-align: center;
}

.text1 {
    max-width: 778px;
    font-size: 64px;
    margin: 4px 88px 12px 88px;
}

.text {
    margin-top: 34.5px;
    white-space: nowrap;
    display: inline;
    font-size: 24px;
}

.text2 {
    max-width: 603px;
    font-size: 22px;
    text-align: center;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! SLIDER !*/

main .slider-text {
    text-align: center;
    margin-bottom: 26px;
    text-transform: uppercase;
    font-size: 28px;
    line-height: 33.6px;
}

main .about-us-container {
    display: flex;
}

main .imageGrid {
    display: flex;
    overflow: hidden;
    max-width: 2000px;
}

main .imageSet {
    display: flex;
    animation: scrollImages 40s linear infinite;
}

main .mainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

@keyframes scrollImages {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}

main .mainImg {
    width: 133px;
    height: 92px;
    margin-right: 40px;
    object-fit: contain;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! VIDEO !*/

.about-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 78px;
    margin-top: 178px;
}

iframe {
    width: 610px;
    height: 395px;
    border-radius: 24px;
    margin: 0;
}

.text1-container {
    height: auto;
    max-width: 552px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! SOME NUM !*/

.statisticsDatacon {
    margin-top: 107px;
}

.statisticsData {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0px;
    gap: 20px;
    width: 235px;
    height: 224px;
    background: #eb8724;
    border-radius: 32px;
    padding: 30px;
}

.statisticsData h3 {
    margin: 0 auto;
    width: 235px;
    height: 96px;
    font-family: 'RoadUI-Regular', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 80px;
    line-height: 96px;
    display: flex;
    align-items: center;
}

.statisticsData p {
    margin: 0 auto;
    width: 235px;
    height: 93px;
    font-family: 'RoadUI-Regular', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 31px;
    display: flex;
    align-items: center;
    flex: none;
    order: 1;
    align-self: stretch;
    flex-grow: 0;
}

.statistics {
    max-width: 1240px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 20px;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! services !*/

.services-container {
    display: flex;
    gap: 64px;
    margin-top: 109px;
    align-items: center;
}

.services-container.normal {
    flex-direction: row;
}

.services-container.reversed {
    flex-direction: row-reverse;
}

.mainCon {
    background-color: rgba(39, 46, 54, 1);
    border-radius: 32px;
    padding: 50px;
    margin-top: 100px;
}

.inf-con {
    max-width: 560px;
}

.language-phone-container__phone {
    color: #fbb216;
    font-size: 20px;
    font-weight: 600;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/

.completed-cart {
    border: 2px solid #fbb216;
    border-radius: 24px;
    padding: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    box-sizing: border-box;
}

.cimg {
    width: 546px;
    height: 285px;
    gap: 0px;
    border-radius: 16px 0px 0px 0px;
    opacity: 0px;
}

.ctext {
    font-family: Road UI;
    font-size: 20px;
    font-weight: 600;
    line-height: 28px;
    text-align: center;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
}

.completed-works-container {
    display: flex;
    justify-content: center;
    max-width: 1240px;
    margin: 32px auto;
}

.completed-works-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(540px, 1fr));
    gap: 32px;
    width: 100%;
    max-width: 1240px;
    margin: 0 auto;
}

.Label {
    font-family: Road UI;
    font-size: 24px;
    font-weight: 600;
    line-height: 28.8px;
    text-align: center;
    color: #9b2915;
    margin-bottom: -46px;
}

.heading3 {
    font-family: Montserrat;
    font-size: 48px;
    font-weight: 700;
    line-height: 57.6px;
    text-align: center;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
}

.completed-text {
    text-align: center;
    margin-top: 100px;
}

.imga {
    position: absolute;
    opacity: 1;
    z-index: -1;
}

.background-image {
    width: 1240px;
    height: 576px;
    filter: brightness(60%);
    gap: 0px;
    border-radius: 32px;
    opacity: 0px;
}

.advantages-section {
    margin: auto;
    position: relative;
    max-width: 1240px;
    padding: 50px 20px;
    border-radius: 15px;
    color: white;
    text-align: center;
    z-index: 1;
}

.advantages-section h2 {
    font-size: 36px;
    margin-bottom: 20px;
}

.card {
    display: block;
    background: rgba(255, 255, 255, 0.2);
    /* Прозрачный фон для карточек */
    border-radius: 15px;
    padding: 20px;
    width: auto;
    text-align: center;
    color: white;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    border: 1px solid white;
    backdrop-filter: blur(5px);
}

#our-advantages {
    display: grid;
    grid-template-columns: 3fr 1fr 3fr 1fr;
    grid-template-rows: auto auto;
    margin: 32px 147px;
    gap: 20px;
}

#our-advantages .card {
    grid-column: span 1;
}

#our-advantages .card:nth-child(4),
#our-advantages .card:nth-child(5) {
    grid-column: span 2;
    grid-row: 2;
}

#our-advantages .card:nth-child(1) {
    grid-column: 1 / 2;
}

#our-advantages .card:nth-child(2) {
    grid-column: 2 / 3;
}

#our-advantages .card:nth-child(3) {
    grid-column: 3 / 4;
}

.card p {
    font-size: 16px;
    font-weight: 500;
}